@import url('https://fonts.googleapis.com/css?family=Montserrat');
@import url('https://fonts.googleapis.com/css?family=Muli');


.default-font{
  font-family: 'Muli', Helvetica, sans-serif;
}

.font-white{
  color:white;
}

.backdrop{
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: -1;
}

h1{
  margin-bottom: 1.5rem;
  margin-top: 2rem;
}

.backdrop-half{
  height: 40%;
  width: 100%;
}

.navigation-bar{
  text-align: left;
  display: -ms-inline-flexbox;
  display: inline-flex;
  padding:1em;
  padding-left:8em;
}

.description{
  -ms-flex:1 1;
      flex:1 1;
}

.bg-gradient{
  background-image: -webkit-linear-gradient(110deg, #2b5876 0%, #4e4376 100%);
  background-image: -o-linear-gradient(110deg, #2b5876 0%, #4e4376 100%);
  background-image: linear-gradient(-20deg, #2b5876 0%, #4e4376 100%);
}


.justify{
  text-align: justify;
}

.bold{
  font-weight: bold;
  color:#f09819;
}

.main-background{
  background-color: white;
  top:200;
  margin:7.5em;
  margin-top: 0;
  margin-bottom: 2rem;
  padding:2em;
  padding-top:1em;
  padding-bottom:1em;
  border-radius: 0.9em;
  box-shadow: 10px 10px;
  -webkit-box-shadow: 0px 0px 25px 0px rgba(0,0,0,0.25);
  box-shadow: 0px 0px 25px 0px rgba(0,0,0,0.25);
}

.shadow{
  -webkit-box-shadow: 0px 0px 25px 0px rgba(0,0,0,0.25);
  box-shadow: 0px 0px 25px 0px rgba(0,0,0,0.25);
}

.list-container{
  border-radius:5px;
  margin-bottom:10px;
  padding:10px;
}

.responsive{
  font-size: 1rem;
}

.mobile{
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

@media screen and (max-width: 768px){
  .mobile{
    -ms-flex-direction: column ;
        flex-direction: column ;
    -ms-flex-pack: center;
        justify-content: center;
  }

  .main-background{
    margin:0.5em;
    margin-top: 0;
    padding:0.8em;
  }

  .navigation-bar{
    padding:1em;
    margin:0.5em;
  }

  .navigation-bar h2{
    margin:0;
  }

}

.check-mic{
  background-image: -webkit-linear-gradient(110deg, #fc6076 0%, #ff9a44 100%);
  background-image: -o-linear-gradient(110deg, #fc6076 0%, #ff9a44 100%);
  background-image: linear-gradient(-20deg, #fc6076 0%, #ff9a44 100%);
  border-radius: 3em;
  padding-left:1em;
  padding-right:1em;
  text-align: center;
  position: fixed;
  bottom:10px;
  line-height: 50%;
  right:10px;
  z-index: 100;
}


.check-mic h2{
  font-size: .8rem;
}

.wrap{
  text-align: right;
  margin-bottom: 0.3em;
}

.complete{
  font-weight: 300;
  display: inline-block;
  color:white;
  padding:5px;
  margin:0;
  padding-right:15px;
  padding-left:15px;
  border-radius: 40px;
  background-color: green;
  cursor: pointer;
}

.btn {
  border-radius:25px;
  width:50px;
  height: 50px;
  line-height: 50%;
  background-image: -webkit-gradient(linear, left bottom, left top, from(#1e3c72), color-stop(1%, #1e3c72), to(#2a5298));
  background-image: -webkit-linear-gradient(bottom, #1e3c72 0%, #1e3c72 1%, #2a5298 100%);
  background-image: -o-linear-gradient(bottom, #1e3c72 0%, #1e3c72 1%, #2a5298 100%);
  background-image: linear-gradient(to top, #1e3c72 0%, #1e3c72 1%, #2a5298 100%);
  border: none; /* Remove borders */
  color: white; /* White text */
  padding: 12px 16px; /* Some padding */
  font-size: 16px; /* Set a font size */
  cursor: pointer; /* Mouse pointer on hover */
  text-align: center;
  outline: none !important;
}

.btn:disabled{
  background-color: #E4E4E1;
 background-image: radial-gradient(at top center, rgba(255,255,255,0.03) 0%, rgba(0,0,0,0.03) 100%), -webkit-gradient(linear, left bottom, left top, from(rgba(255,255,255,0.1)), to(rgba(143,152,157,0.60)));
 background-image: -webkit-radial-gradient(at top center, rgba(255,255,255,0.03) 0%, rgba(0,0,0,0.03) 100%), -webkit-linear-gradient(bottom, rgba(255,255,255,0.1) 0%, rgba(143,152,157,0.60) 100%);
 background-image: -o-radial-gradient(at top center, rgba(255,255,255,0.03) 0%, rgba(0,0,0,0.03) 100%), -o-linear-gradient(bottom, rgba(255,255,255,0.1) 0%, rgba(143,152,157,0.60) 100%);
 background-image: radial-gradient(at top center, rgba(255,255,255,0.03) 0%, rgba(0,0,0,0.03) 100%), linear-gradient(to top, rgba(255,255,255,0.1) 0%, rgba(143,152,157,0.60) 100%);
 	background-blend-mode: normal, multiply;
}


.submit{
  border: none;
  border-radius: 4px;
  background-image: -webkit-linear-gradient(135deg, #6e45e2 0%, #88d3ce 100%);
  background-image: -o-linear-gradient(135deg, #6e45e2 0%, #88d3ce 100%);
  background-image: linear-gradient(-45deg, #6e45e2 0%, #88d3ce 100%);
  cursor: pointer;
}

.submit:hover{
  background: -webkit-linear-gradient(315deg, #667eea 0%, #764ba2 100%);
  background: -o-linear-gradient(315deg, #667eea 0%, #764ba2 100%);
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  -webkit-transition: background-color 1000ms linear;
  -o-transition: background-color 1000ms linear;
  transition: background-color 1000ms linear;
}

.submit:active{
  background-image: -webkit-gradient(linear, left bottom, left top, from(#5f72bd), to(#9b23ea));
  background-image: -webkit-linear-gradient(bottom, #5f72bd 0%, #9b23ea 100%);
  background-image: -o-linear-gradient(bottom, #5f72bd 0%, #9b23ea 100%);
  background-image: linear-gradient(to top, #5f72bd 0%, #9b23ea 100%);
}

/* Darker background on mouse-over */
.btn:hover {
  background-image: -webkit-linear-gradient(150deg, #ff5858 0%, #f09819 100%);
  background-image: -o-linear-gradient(150deg, #ff5858 0%, #f09819 100%);
  background-image: linear-gradient(-60deg, #ff5858 0%, #f09819 100%);
}

.recording{
  background-image: -webkit-linear-gradient(110deg, #2b5876 0%, #4e4376 100%);
  background-image: -o-linear-gradient(110deg, #2b5876 0%, #4e4376 100%);
  background-image: linear-gradient(-20deg, #2b5876 0%, #4e4376 100%);
}

.pulse {
  outline: none !important;
  display: block;
  cursor: pointer;
  -webkit-box-shadow: 0 0 0 rgba(43, 88, 118, 0.9);
          box-shadow: 0 0 0 rgba(43, 88, 118, 0.9);
  -webkit-animation: pulse .8s infinite;
          animation: pulse .8s infinite;
}


@-webkit-keyframes pulse {
  0% {
    -webkit-box-shadow: 0 0 0 0 rgba(43, 88, 118, 0.4);
  }
  70% {
      -webkit-box-shadow: 0 0 0 10px rgba(43, 88, 118, 0);
  }
  100% {
      -webkit-box-shadow: 0 0 0 0 rgba(43, 88, 118, 0);
  }
}
@keyframes pulse {
  0% {
    -webkit-box-shadow: 0 0 0 0 rgba(43, 88, 118, 0.4);
            box-shadow: 0 0 0 0 rgba(43, 88, 118, 0.4);
  }
  70% {
      -webkit-box-shadow: 0 0 0 10px rgba(43, 88, 118, 0);
              box-shadow: 0 0 0 10px rgba(43, 88, 118, 0);
  }
  100% {
      -webkit-box-shadow: 0 0 0 0 rgba(204,169,44, 0);
              box-shadow: 0 0 0 0 rgba(204,169,44, 0);
  }
}

.error{
  color: rgb(183, 0, 3);
  margin-top:.5rem;
  margin-bottom:.5rem;
}

.loader{
  width:50px;
  height: 50px;
  margin-right: 10px;
  border-radius: 100%;
  -webkit-animation-name: stretch;
          animation-name: stretch;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s; 
  -webkit-animation-timing-function: ease-out; 
          animation-timing-function: ease-out; 
  -webkit-animation-delay: 0; 
          animation-delay: 0;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-fill-mode: none;
          animation-fill-mode: none;
  -webkit-animation-play-state: running;
          animation-play-state: running;
}

@-webkit-keyframes stretch {
  0% {
    -webkit-transform: scale(.8);
            transform: scale(.8);
    background-color: red;
  }
  50% {
    background-color: orange;
  }
  100% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    background-color: green;
  }
}

@keyframes stretch {
  0% {
    -webkit-transform: scale(.8);
            transform: scale(.8);
    background-color: red;
  }
  50% {
    background-color: orange;
  }
  100% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    background-color: green;
  }
}


